@import "@/assets/global.scss";




















































































.main-content {
  box-sizing: border-box;
  margin-top: 24px;
}

.test-table {
  width: 300px;
  border-collapse: collapse;
  margin-bottom: 20px;

  > .table-row:first-child {
    height: 48px;
    display: flex;
    border-top: 1px solid #CBCBCB;
    border-bottom: 1px solid #CBCBCB;
  }

  > .table-row {
    height: 48px;
    display: flex;
    border-bottom: 1px solid #CBCBCB;
  }
}

.table-col:first-child {
  @extend .font-14-normal;
  padding: 12px;
  text-align: left;
  border-left: 1px solid #CBCBCB;
  border-right: 1px solid #CBCBCB;
}

.table-col {
  @extend .font-14-normal;
  padding: 12px;
  text-align: left;
  border-right: 1px solid #CBCBCB;
}

.table-col-btn:first-child {
  @extend .font-14-normal;
  padding: 12px;
  text-align: center;
  color: #C8553D;
  border-left: 1px solid #CBCBCB;
}

.table-col-btn:last-child {
  @extend .font-14-normal;
  padding: 12px;
  text-align: center;
  color: #C8553D;
  border-right: 1px solid #CBCBCB;
}
